.stars {
  display: flex;
}

.stars__star {
  margin-right: 4px;

  height: 16px;
  width: 16px;

  background-image: url(../images/star.svg);
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 0.5px;
}

.stars__star--active {
  background-image: url(../images/star-active.svg);
}
